<template><section><html><head></head><body><h1>ImageryLayer</h1>
<p><code v-pre="">vc-layer-imagery</code> 组件用于加载 Cesium 影像，可以直接指定 <code v-pre="">vc-layer-imagery</code> 的 <code v-pre="">imageryProvider</code> 加载影像，也可以挂载子组件 <code v-pre="">vc-provider-imager-xxx</code> 加载影像。</p>
<h2>示例</h2>
<h3>加载影像图层到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-layer-imagery :alpha="alpha" :brightness="brightness" :contrast="contrast">
          <vc-provider-imagery-tianditu map-style="img_c" token="436ce7e50d27eede2f2929307e6b33c0"></vc-provider-imagery-tianditu>
        </vc-layer-imagery>
      </vc-viewer>
      <div class="demo-tool">
        <span>透明度</span>
        <vue-slider v-model="alpha" :min="0" :max="1" :interval="0.01"></vue-slider>
        <span>亮度</span>
        <vue-slider v-model="brightness" :min="0" :max="3" :interval="0.01"></vue-slider>
        <span>对比度</span>
        <vue-slider v-model="contrast" :min="0" :max="3" :interval="0.01"></vue-slider>
      </div>
    </div>
  </template>
  
</doc-preview>
<h4>代码</h4>
<pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"viewer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">vc-viewer</span> <span class="hljs-attr">@ready</span>=<span class="hljs-string">"ready"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vc-layer-imagery</span> <span class="hljs-attr">:alpha</span>=<span class="hljs-string">"alpha"</span> <span class="hljs-attr">:brightness</span>=<span class="hljs-string">"brightness"</span> <span class="hljs-attr">:contrast</span>=<span class="hljs-string">"contrast"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-provider-imagery-tianditu</span>
          <span class="hljs-attr">map-style</span>=<span class="hljs-string">"img_c"</span>
          <span class="hljs-attr">token</span>=<span class="hljs-string">"436ce7e50d27eede2f2929307e6b33c0"</span>
        &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-provider-imagery-tianditu</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-layer-imagery</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-tool"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>透明度<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vue-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"alpha"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"1"</span> <span class="hljs-attr">:interval</span>=<span class="hljs-string">"0.01"</span>  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-slider</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>亮度<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vue-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"brightness"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:interval</span>=<span class="hljs-string">"0.01"</span>  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-slider</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>对比度<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">vue-slider</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"contrast"</span> <span class="hljs-attr">:min</span>=<span class="hljs-string">"0"</span> <span class="hljs-attr">:max</span>=<span class="hljs-string">"3"</span> <span class="hljs-attr">:interval</span>=<span class="hljs-string">"0.01"</span>  &gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vue-slider</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="language-javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">alpha</span>: <span class="hljs-number">1</span>,
        <span class="hljs-attr">brightness</span>: <span class="hljs-number">1</span>,
        <span class="hljs-attr">contrast</span>: <span class="hljs-number">1</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      ready (cesiumInstance) {
        <span class="hljs-keyword">const</span> {<span class="hljs-title class_">Cesium</span>, viewer} = cesiumInstance
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
<h2>属性</h2>
<!-- prettier-ignore -->
<table>
<thead>
<tr>
<th>属性名</th>
<th>类型</th>
<th>默认值</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>rectangle</td>
<td>Object</td>
<td><code v-pre="">imageryProvider.rectangle</code></td>
<td><code v-pre="">optional</code>图层的矩形范围,此矩形限制了影像可见范围。</td>
</tr>
<tr>
<td>alpha</td>
<td>Number|function</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>图层透明度值，取值范围为 0.0~1.0。</td>
</tr>
<tr>
<td>brightness</td>
<td>Number|function</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>图层亮度值。值为 1.0 表示使用原图；值大于 1.0 时图像将变亮；值小于 1.0 时图像将变暗。</td>
</tr>
<tr>
<td>contrast</td>
<td>Number|function</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>图层对比度。值为 1.0 表示使用原图；值大于 1.0 表示增加对比度；值小于 1.0 表示降低对比度。</td>
</tr>
<tr>
<td>hue</td>
<td>Number|function</td>
<td><code v-pre="">0.0</code></td>
<td><code v-pre="">optional</code>图层色调。值为 0.0 表示使用原图。</td>
</tr>
<tr>
<td>saturation</td>
<td>Number|function</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>图层饱和度。值为 1.0 表示使用原图；值大于 1.0 表示增加饱和度；值小于 1.0 表示降低饱和度。</td>
</tr>
<tr>
<td>gamma</td>
<td>Number|function</td>
<td><code v-pre="">1.0</code></td>
<td><code v-pre="">optional</code>图层伽马校正。值为 1.0 表示使用原图。</td>
</tr>
<tr>
<td>splitDirection</td>
<td>Number</td>
<td><code v-pre="">0</code></td>
<td><code v-pre="">optional</code>指定影像图层分割方向。 <strong>LEFT: -1, NONE: 0, RIGHT: 1</strong></td>
</tr>
<tr>
<td>minificationFilter</td>
<td>Number</td>
<td><code v-pre="">9729</code></td>
<td><code v-pre="">optional</code> 指定图层纹理缩小过滤器。 可能的值是 TextureMinificationFilter.LINEAR 和 TextureMinificationFilter.NEAREST。<strong>NEAREST: 9728, LINEAR: 9729, NEAREST_MIPMAP_NEAREST: 9984, LINEAR_MIPMAP_NEAREST: 9985, NEAREST_MIPMAP_LINEAR: 9986, NEAREST_MIPMAP_NEAREST: 9984</strong></td>
</tr>
<tr>
<td>magnificationFilter</td>
<td>Number</td>
<td><code v-pre="">9729</code></td>
<td><code v-pre="">optional</code> 指定图层纹理缩小过滤器。 可能的值是 TextureMagnificationFilter.LINEAR 和 TextureMagnificationFilter.NEAREST。 <strong>NEAREST: 9728, LINEAR: 9729</strong></td>
</tr>
<tr>
<td>show</td>
<td>Boolean</td>
<td><code v-pre="">true</code></td>
<td><code v-pre="">optional</code> 指定图层是否显示，如果显示图层，则为 true; 否则，false。</td>
</tr>
<tr>
<td>maximumAnisotropy</td>
<td>Number</td>
<td><code v-pre="">maximum supported</code></td>
<td><code v-pre="">optional</code> 指定纹理过滤的最大各向异性级别。 如果未指定此参数，则将使用 WebGL 堆栈支持的最大各向异性。 较大的值使图像在水平视图中看起来更好。</td>
</tr>
<tr>
<td>minimumTerrainLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>最小地形细节层次。level 0 是最小细节层次。</td>
</tr>
<tr>
<td>maximumTerrainLevel</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code>最大地形细节层次。</td>
</tr>
<tr>
<td>cutoutRectangle</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定裁剪此影像图层的矩形范围。 <strong>结构：{ west: number, south: number, east: number, north: number }</strong></td>
</tr>
<tr>
<td>colorToAlpha</td>
<td>Object</td>
<td></td>
<td><code v-pre="">optional</code> 指定透明时的颜色。</td>
</tr>
<tr>
<td>colorToAlphaThreshold</td>
<td>Number</td>
<td><code v-pre="">0.004</code></td>
<td><code v-pre="">optional</code> 颜色到alpha的阈值。</td>
</tr>
<tr>
<td>sortOrder</td>
<td>Number</td>
<td></td>
<td><code v-pre="">optional</code> 图层相对顺序。</td>
</tr>
</tbody>
</table>
<hr>
<ul>
<li>参考官方文档： <strong><a href="https://cesium.com/docs/cesiumjs-ref-doc/ImageryLayer.html">ImageryLayer</a></strong></li>
</ul>
<h2>事件</h2>
<table>
<thead>
<tr>
<th>事件名</th>
<th>参数</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>ready</td>
<td>{Cesium, viewer, cesiumObject}</td>
<td>该组件渲染完毕时触发，返回 Cesium 类, viewer 实例，以及当前组件的 cesiumObject。</td>
</tr>
</tbody>
</table>
<hr>
</body></html></section></template>

<script>
    export default {
      data () {
        return {
          alpha: 1,
          brightness: 1,
          contrast: 1
        }
      },
      methods: {
        ready (cesiumInstance) {
          const {Cesium, viewer} = cesiumInstance
        }
      }
    }
  </script>